const partnersSwiper = new Swiper('.partners__swiper', {
  // Optional parameters
  direction: 'horizontal',
  slidesPerView: 5,
  spaceBetween: 50,


  // Navigation arrows
  navigation: {
    nextEl: '.partners__swiper-button-next',
    prevEl: '.partners__swiper-button-prev',
  },


  breakpoints: {
    320: {
      slidesPerView: 1,
      spaceBetween: 20
    },
    500: {
      slidesPerView: 2,
      spaceBetween: 20
    },
    1030: {
      slidesPerView: 3,
      spaceBetween: 20
    },
    1300: {
      slidesPerView: 4,
      spaceBetween: 30
    },
    1670: {
      slidesPerView: 5,
      spaceBetween: 40
    }
  }
});