const mainSwiper = new Swiper('.main-swiper', {
    // Default parameters
    slidesPerView: 1,
    spaceBetween: 10,

    navigation: {
        nextEl: '.hero__swiper-button-next',
        prevEl: '.hero__swiper-button-prev',
    },

    // Responsive breakpoints
    // breakpoints: {
    //     // when window width is >= 320px
    //     320: {
    //         slidesPerView: 2,
    //         spaceBetween: 20
    //     },
    //     // when window width is >= 480px
    //     480: {
    //         slidesPerView: 3,
    //         spaceBetween: 30
    //     },
    //     // when window width is >= 640px
    //     640: {
    //         slidesPerView: 4,
    //         spaceBetween: 40
    //     }
    // }
})


const thumbSwiper = new Swiper('.thumb-swiper', {
    // Default parameters
    slidesPerView: 1,
    spaceBetween: 10,
    initialSlide: 1,


    // Responsive breakpoints
    // breakpoints: {
    //     // when window width is >= 320px
    //     320: {
    //         slidesPerView: 2,
    //         spaceBetween: 20
    //     },
    //     // when window width is >= 480px
    //     480: {
    //         slidesPerView: 3,
    //         spaceBetween: 30
    //     },
    //     // when window width is >= 640px
    //     640: {
    //         slidesPerView: 4,
    //         spaceBetween: 40
    //     }
    // }
})

// Синхронизация с учетом смещения на один
mainSwiper.on('slideChange', () => {
    const mainIndex = mainSwiper.activeIndex;
    thumbSwiper.slideTo(mainIndex + 1); // Перемещаем thumb-swiper на слайд +1
});

thumbSwiper.on('click', (swiper) => {
    const clickedIndex = swiper.clickedIndex;
    if (clickedIndex !== undefined) {
        mainSwiper.slideTo(clickedIndex - 1); // Перемещаем main-swiper с учетом -1
    }
});